import { configureStore, combineReducers } from "@reduxjs/toolkit";
import { persistReducer, persistStore } from "redux-persist";
import storage from "redux-persist/lib/storage";
import userReducer, { setToken, connectedToggle } from "./userSlice"; // Importez setToken et connectedToggle depuis le userSlice
import productReducer from "./productSlice";
import cartReducer from "./cartSlice";

// Combiner les diffrents reducers en un seul rootReducer
const rootReducer = combineReducers({
  user: userReducer,
  product: productReducer,
  cart: cartReducer,
});

// Configuration pour redux-persist
const persistConfig = {
  key: "data", // La cl pour stocker les donnes dans le local storage
  storage, // Le type de stockage (ici, on utilise le local storage)
  whitelist: ["cart", "user"], // Permet de slectionner les slices  sauvegarder
  storageOptions: {
    expires: "3d", // Dure de conservation de 3 jours
    expireKey: "persistExpires", // Cl pour stocker la date d'expiration dans le localStorage
  },
};

// Utilisation de persistReducer pour crer un reducer qui supporte le local storage
const persistedReducer = persistReducer(persistConfig, rootReducer);

// Cration du store avec le reducer persistant
const store = configureStore({
  reducer: persistedReducer,
});

// Cration d'un persistor qui permet de sauvegarder automatiquement le store dans le local storage
export const persistor = persistStore(store);

// Exportez  la fois le store et le persistor
export { store };



